.app-wrapper {
    display: grid;
    grid-template-areas:
    "h h"
    "n c";
    grid-template-rows: 80px 1fr;
    grid-template-columns: 2fr 10fr;
    width: 1200px;
    margin: 0 auto;
    /* grid-gap: 10px; */
}

.app-wrapper-content {
    grid-area: c;
    background-color: rgb(231, 227, 185);
}